import React, { Component } from "react";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        { completed: false, text: "Walk the dog" },
        { completed: false, text: "Pick up the trash" },
        { completed: true, text: "Do the dishes" }
      ]
    };
  }

  render() {
    return (
      <div className="m-3">
        <h1>Todo</h1>
        <ul className="list-group">
          {this.state.items.map(item => (
            <li
              key={item.text}
              className="list-group-item"
              style={
                item.completed
                  ? { textDecoration: "line-through", color: "lightgrey" }
                  : {}
              }
            >
              {item.text}
              <button
                onClick={() => {
                  this.handleRemove(item);
                }}
                className="btn btn-danger float-right"
              >
                Remove
              </button>
              <button
                onClick={() => {
                  this.handleDone(item);
                }}
                className="btn btn-success mr-2 float-right"
              >
                Done
              </button>
            </li>
          ))}
        </ul>
      </div>
    );
  }

  handleRemove(itemToRemove) {
    var newItems = this.state.items.filter(i => i != itemToRemove);
    this.setState({
      items: newItems
    });
    console.log(newItems);
  }

  handleDone(itemToBeDone) {
    var updatedItems = this.state.items;
    var index = updatedItems.indexOf(itemToBeDone);
    updatedItems[index].completed = true;
    this.setState({
      items: updatedItems
    });
    console.log(index);
  }
}

export default App;
